.App {
  font-family: sans-serif;
  text-align: center;
}

.flex-box {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.flex-item {
  color: white;
  border-radius: 15px;
  margin: 10px;
  padding: 10px;
  background-color: #03203c;
}

.userImage {
  border-radius: 60%;
  border: 1px solid white;
  display: block;
  margin: 0 auto;
}

.details {
  text-align: center;
}

.button {
  background-color: #0d0d0d; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 7px;
}
